import React, {Component} from 'react';
import "../css/Recipes.css";
import {Link} from 'react-router-dom';
import {IoIosArrowDropleftCircle} from "react-icons/io";
import {FaRegDotCircle} from "react-icons/fa";

class RecipeArticle extends Component {

    constructor() {
        super();
        this.state = {
            recipe: {},
        };
      }

    componentDidMount() {
        const fetchData = async () => {
          const result = await fetch(`/api${window.location.pathname}`)
          const body = await result.json();
          this.setState({
            recipe: body[0]
          }, document.title = () => (document.title = `${this.state.recipe.recipeName} - The Recommended Dose`));
        }
        fetchData()
      }


    render(){

        const recipe = this.state.recipe;

        return(
            <>
            <Link to="/recipes">
                <IoIosArrowDropleftCircle id="rcpaReturnIcon"/>
            </Link>
            
            <div id="rcpaWrapper">

                <h1 id="rcpaHeader">{recipe.recipeName}</h1>

                <div id="rcpaImageDiv" >
                    <img id="rcpaImage" src={recipe.url} alt={recipe.recipeName}/>
                </div>

                <div className="rcpaDiv" id="rcpaDescDiv">
                    <span>{recipe.description}</span>
                </div>

                <div className="rcpaDiv" id="rcpaInfoDiv">
                    <span>Time: {recipe.estTime}</span>
                    <span>Servings: {recipe.servings}</span>
                </div>

                <div className="rcpaDiv" id="rcpaIngDiv">
                    <h2>Ingredients</h2>
                    {String(recipe.ingredients).split('\n').map((i,key) => {
                        return <div key={key}><FaRegDotCircle id="rcpaIngIcon"/> {i}<br /></div>;
                    })}
                </div>

                <div className="rcpaDiv" id="rcpaDirDiv">
                    <h2>Directions</h2>
                    {String(recipe.directions).split('\n').map((i,key) => {
                        return <div id="rcpaDirDiv2" key={key}>
                                    <span id="rcpaDirNums">{key+1}</span>
                                    <span id="rcpaDirText">{i}</span><br />
                                </div>;
                    })}
                </div>

                {recipe.bonusTips !== "" ?
                    <div className="rcpaDiv" id="rcpaBTDiv">
                        <h3>Bonus Tips</h3>

                        {String(recipe.bonusTips).split('\n').map((i,key) => {
                        return <div key={key}>{i}<br /></div>;
                    })}
                    </div>
                : null }

            </div>
            </>
        )
    }
}


export default RecipeArticle;